\n\n    &-code-block {\n        display: flex;\n        position: absolute;\n        top: 0;\n        right: 0;\n        z-index: 10;\n        padding: 6px 6px;\n\n        &__copied {\n            display: none;\n            font-size: 14px;\n        }\n\n        &_success {\n            .docs-markdown-code-block__copied {\n                display: block;\n            }\n\n            .docs-markdown__code-icon {\n                display: none;\n            }\n        }\n    }\n}\n\n.docs-header-link {\n    position: relative;\n\n    & span {\n        position: absolute;\n        display: block;\n        left: 0;\n        top: -60px;\n    }\n}\n\n@mixin docs-markdown-theme($theme) {\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $foreground: map-get($theme, foreground);\n    $is-dark: map-get($theme, is-dark);\n\n    $text: mc-color($foreground, text);\n    $color: mc-color($primary);\n\n    $link: map-get(map-get($theme, components), link);\n    $color_hover: map-get($link, state-hover-text);\n\n    $color_disabled: mc-color($foreground, text-disabled);\n    $fg-default: mc-color($foreground, text);\n    $th-color: mc-color($second, 400);\n    $divider: mc-color($foreground, divider);\n\n    .docs-header-link {\n        color: $text;\n    }\n\n    .docs-markdown__pre .docs-markdown__code,\n    .docs-markdown-code-block {\n        //colors from solarized and Darcula color schemes\n        background: if($is-dark, #2b2b2b, #fdf6e3);\n        color: if($is-dark, #bababa, #657b83);\n    }\n\n    .docs-markdown {\n        color: $fg-default;\n\n        &__a {\n            color: $color;\n            border-bottom: 1px solid rgba($color, 0.5);\n\n            &:focus {\n                outline: none;\n            }\n\n            &:visited {\n                color: $color;\n            }\n\n            &:hover {\n                color: $color_hover;\n                border-bottom-color: rgba($color_hover, 0.5);\n            }\n\n            &.cdk-keyboard-focused {\n                outline: $color solid 2px;\n                outline-offset: 2px;\n            }\n\n            &[disabled] {\n                color: $color_disabled;\n            }\n        }\n\n        &__caption {\n            color: $color_disabled;\n        }\n\n        &__thead {\n            box-shadow: 0 1px 0 $divider;\n        }\n\n        &__th {\n            color: $th-color;\n        }\n\n        &__code-icon {\n            color: $color;\n        }\n    }\n\n\n    .docs-example-viewer__source {\n        position: relative;\n    }\n}\n\n@mixin docs-markdown-typography($config) {\n\n    .docs-header-link {\n        &_3 {\n            @include mc-typography-level-to-styles($config, headline);\n        }\n\n        &_4 {\n            @include mc-typography-level-to-styles($config, body-strong);\n        }\n    }\n\n    .docs-markdown {\n        &__p, &__a, &__tr {\n            @include mc-typography-level-to-styles($config, body);\n        }\n\n        &__caption,\n        &__th {\n            @include mc-typography-level-to-styles($config, caption);\n        }\n    }\n}\n\n\n@mixin docs-linc($color, $color_hover, $color_disabled) {\n    color: $color;\n    border-bottom: 1px solid rgba($color, 0.5);\n\n    &:focus {\n        outline: none;\n    }\n\n    &:visited {\n        color: $color;\n    }\n\n    &:hover {\n        color: $color_hover;\n        border-bottom-color: rgba($color_hover, 0.5);\n    }\n\n    &.cdk-keyboard-focused {\n        outline: $color solid 2px;\n        outline-offset: 2px;\n    }\n\n    &[disabled] {\n        color: $color_disabled;\n    }\n}\n","\n.mc-tab-group {\n    // TODO: need none\n    text-align: left !important;\n}\n","/*\n Darcula color scheme from the JetBrains family of IDEs\n */\n@mixin material-dark(){\n    .hljs {\n        display: block;\n        overflow-x: auto;\n        padding: 0.5em;\n    }\n\n    .hljs,\n    .docs-example-source-copy {\n        background: #2b2b2b;\n        col